<template>
  <div class="ArticleCard-warp">
    <h5 class="mt-0 mb-1" style="font-width:900;border-left:6px solid #3babc7;padding-left:12px">{{title}}</h5>

    <b-media left-align vertical-align="center" style="padding-top:16px;color:#707070;">
      <b-img
        slot="aside"
        class="border-radius4"
        :src="iconImg"
        height="80"
        width="80"
        blank-color="#ccc"
      />
      <p class="mb-0 ArticleCard-content">{{content}}</p>
    </b-media>
    <b-media class="small-font" style="padding-top:8px;">
      <b-div>
        <b-img :src="authorIcon" class="border-radius3 justify-content" blank-color="#ccc" width="20"/>
        {{author}}
      </b-div>
      <b-div>
        <b-img :src="$store.state.WebDefaultModule.defaultWatchIcon" blank-color="#ccc" width="20"/>
        {{watch}}
      </b-div>
      <b-div>
        <b-img :src="$store.state.WebDefaultModule.defaulCollectIcon" blank-color="#ccc" width="20"/>
        {{collect}}
      </b-div>
      <b-div>
        <b-img :src="$store.state.WebDefaultModule.defaulCommentIcon" blank-color="#ccc" width="20"/>
        {{comment}}
      </b-div>

      <b-div style="float:right;">
        <!-- time -->
        <b-div>
          <b-img :src="defaulPublishTimeIcon" blank-color="#ccc" width="20"/>
          {{publishTime}}
        </b-div>
        <b-div v-for="(item,index) in lable" :key="index">
          <!-- article-lable -->
          <b-article-lable class="article-lable">{{item}}</b-article-lable>
        </b-div>
        <b-div>
          <!-- article-from -->
          <b-article-lable class="article-from">{{from}}</b-article-lable>
        </b-div>
      </b-div>
    </b-media>
  </div>
</template>

<script>
export default {
  props: {
    id: String,
    title: String,
    content: String, //正文
    iconImg: String, //图片展示
    publishTime: String, //发表时间
    authorIcon: String, //作者头像
    author: String, //作者
    watch: String, //阅读量
    comment: String, //评论
    collect: String, //收藏
    from: String, //原创、转载
    lable: {} //文章标签：VUE JAVA SpringBoot ...
  },

  data() {
    return {
      defaultWatchIcon: "/card/watch.png",
      defaulCollectIcon: "/card/collect.png",
      defaulCommentIcon: "/card/comment.png",
      defaulPublishTimeIcon: "/card/time.png"
    };
  }
};
</script>

<style scoped>
.ArticleCard-warp {
  padding: 20px;
  background: rgba(255, 255, 255, 0.8);
  cursor: pointer;
}

.ArticleCard-content {
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
}
</style>


